import React from "react";
import Form from "react-bootstrap/Form";
import Alert from "react-bootstrap/Alert";
import Button from "react-bootstrap/Button";
import VanillaTilt from "vanilla-tilt";

export function Item11() {}

export function Item12() {
  const Greeting = () => {
    const [name, setName] = React.useState("");
    const handleChange = (event) => setName(event.target.value);

    return (
      <div>
        <Form>
          <Form.Label htmlFor="name">Name</Form.Label>
          <Form.Control
            placeholder="Please type your name"
            onChange={handleChange}
            id="name"
          />
        </Form>
        <br />
        {name ? <Alert>Hello {name}</Alert> : ""}
      </div>
    );
  };

  return <Greeting />;
}

export function Item13() {
  const Greeting = () => {
    const [name, setName] = React.useState(
      window.localStorage.getItem("name") || ""
    );

    React.useEffect(() => {
      window.localStorage.setItem("name", name);
    });

    const handleChange = (event) => setName(event.target.value);

    return (
      <div>
        <Form>
          <Form.Label htmlFor="name">Name</Form.Label>
          <Form.Control value={name} onChange={handleChange} id="name" />
          {name ? (
            ""
          ) : (
            <Form.Text className="text-muted">Please type your name</Form.Text>
          )}
        </Form>
        <br />
        {name ? <Alert>Hello {name}</Alert> : ""}
      </div>
    );
  };

  return <Greeting />;
}

