import React from "react";
import PropTypes from "prop-types";
import Alert from "react-bootstrap/Alert";

export function Item01() {}

export function Item02() {
  return React.createElement("div", {
    className: "item02-container",
    children: "Hello World"
  });
}

export function Item03() {
  return <div className="item03-container">Hello World</div>;
}

export function Item04() {
  const children = (
    <div>
      <span>Hello</span> <strong>World</strong>
    </div>
  );
  const className = "item04-container";
  const id = "item04-id";
  const props = { className, id, children };

  const element = <div {...props} />;
  // console.log(element);

  return element;
}

export function Item05() {
  const element = (
    <>
      <span>Hello</span> <span>World</span>
    </>
  );
  return element;
}

export function Item06() {
  const Message = ({ children }) => {
    return <div className="message">{children}</div>;
  };

  return (
    <div className="item06-container">
      <Message>Hello World</Message>
    </div>
  );
}

export function Item07() {
  const SayHello = ({ firstName, lastName }) => {
    return (
      <div>
        Hello {firstName} {lastName}!
      </div>
    );
  };
  SayHello.propTypes = {
    firstName: PropTypes.string.isRequired,
    lastName: PropTypes.string.isRequired
  };

  return <SayHello firstName={false} />;
}

export function Item08() {
  function CharacterCount({ text }) {
    return (
      <Alert variant="light">
        {`The text "${text}" has `}
        {text.length ? <strong>{text.length}</strong> : "No"}
        {" characters"}
      </Alert>
    );
  }

  return (
    <>
      <CharacterCount text="Hello World" />
      <CharacterCount text="" />
    </>
  );
}

