import React from "react";
import Form from "react-bootstrap/Form";
import Alert from "react-bootstrap/Alert";
import Button from "react-bootstrap/Button";
import FloatingLabel from "react-bootstrap/FloatingLabel";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Stack from "react-bootstrap/Stack";

export function Item19() {
  const UsernameForm = () => {
    const handleSubmit = (event) => {
      event.preventDefault();
      const username = event.target.elements.usernameInput.value;
      alert(`You entered: ${username}`);
    };

    return (
      <Form onSubmit={handleSubmit}>
        <Form.Group className="mb-3" controlId="usernameInput">
          <Form.Label>Username</Form.Label>
          <Form.Control type="text" />
        </Form.Group>
        <Button variant="primary" type="submit">
          Submit
        </Button>
      </Form>
    );
  };

  return <UsernameForm />;
}

export function Item20() {
  const UsernameForm = () => {
    const [username, setUsername] = React.useState("");
    const [displayName, setDisplayName] = React.useState("");
    const isLowerCase = username === username.toLowerCase();
    const error = isLowerCase ? null : "Username must be lower case";

    const handleSubmit = (event) => {
      event.preventDefault();
      setDisplayName(username);
    };

    const handleChange = (event) => {
      setDisplayName("");
      setUsername(event.target.value);
    };

    return (
      <Container>
        <Row>
          <Col>
            <Form onSubmit={handleSubmit}>
              <Stack direction="horizontal" gap={3}>
                <Button
                  className="ms-auto"
                  variant="primary"
                  disabled={Boolean(error)}
                  type="submit"
                >
                  Submit
                </Button>
                <Form.Control
                  placeholder="Your name"
                  type="text"
                  onChange={handleChange}
                />
              </Stack>

              {!Boolean(error) || (
                <Alert className="mt-3" variant="danger">
                  {error}
                </Alert>
              )}
              {!Boolean(displayName) || (
                <Alert className="mt-3" variant="primary">
                  Hello {displayName}
                </Alert>
              )}
            </Form>
          </Col>
          <Col></Col>
        </Row>
      </Container>
    );
  };

  return <UsernameForm />;
}

export function Item21() {
  const UsernameForm = () => {
    const [username, setUsername] = React.useState("");

    function handleSubmit(event) {
      event.preventDefault();
      alert(`You entered: ${username}`);
    }

    const handleChange = (event) => {
      setUsername(event.target.value.toLowerCase());
    };

    return (
      <Form onSubmit={handleSubmit}>
        <FloatingLabel
          controlId="floatingInput"
          label="Username"
          className="mb-3"
        >
          <Form.Control type="text" onChange={handleChange} value={username} />
        </FloatingLabel>

        <Button variant="primary" type="submit">
          Submit
        </Button>
      </Form>
    );
  };

  return <UsernameForm />;
}

export function Item22() {}

export function Item23() {
  const allItems = [
    { id: "a", value: "apple" },
    { id: "o", value: "orange" },
    { id: "g", value: "grape" },
    { id: "p", value: "pear" }
  ];

  const [items, setItems] = React.useState(allItems);

  function addItem() {
    setItems([...items, allItems.find((i) => !items.includes(i))]);
  }

  function removeItem(item) {
    setItems(items.filter((i) => i !== item));
  }

  return (
    <div>
      <Button
        className="mb-2"
        variant="success"
        disabled={items.length >= allItems.length}
        onClick={addItem}
      >
        add item
      </Button>
      <Row>
        {items.map((item) => (
          <Col>
            <Form>
              <Form.Label>{item.value}</Form.Label>
              <Form.Control
                className="mb-3"
                placeholder={item.value}
                defaultValue={item.value}
              />
            </Form>
            <Button variant="danger" onClick={() => removeItem(item)}>
              remove
            </Button>
          </Col>
        ))}
      </Row>
    </div>
  );
}

export function Item24() {
  const Name = ({ name, onNameChange }) => {
    return (
      <Form.Group className="mb-4">
        <Form.Label>Name</Form.Label>
        <Form.Control value={name} onChange={onNameChange} />
      </Form.Group>
    );
  };

  const FavoriteAnimal = () => {
    const [animal, setAnimal] = React.useState("");
    return (
      <Form.Group className="mb-4">
        <Form.Label>Favorite Animal</Form.Label>
        <Form.Control
          value={animal}
          onChange={(event) => setAnimal(event.target.value)}
        />
      </Form.Group>
    );
  };

  const Display = ({ name }) => {
    return <div>{`Hey ${name}, you are great!`}</div>;
  };

  const [name, setName] = React.useState("");

  return (
    <Form>
      <Name name={name} onNameChange={(event) => setName(event.target.value)} />
      <FavoriteAnimal />
      <Display name={name} />
    </Form>
  );
}

